//body setting default

$font-size-default: $fz-default !default;
$line-height-default: $fz-default-line-height !default;
// $bg-default: #F2F1F7 !default;
// $bg-default: #F7F8FA !default;
$bg-default: #dcdee4 !default;
$font-default: "SF Pro Display", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif !default;

//========Upload==========//
$upload-border-radius: 10px !default;
$upload-border: 2px dashed #f3f6f8 !default;
$ripple-default-color: #f3f6f8 !default;
//===grid======
$breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  menu-break-point: 1200px
) !default;

//==transition===
$base-transition: "all .3s cubic-bezier(0.645, 0.045, 0.355, 1)" !default;
$base-border-radius: 3px;
$base-border-radius2x: 6px;
//=========
